<template>
  <div>
    <div :class="[hasTable?'date-div1':'date-div2']">
      <Button v-if="hasTable" type="success" icon="plus-round" @click="createTable">创建签到表</Button>
    </div>
    <div style="text-align:left;margin:10px">
      <Date-picker v-model="date" @on-change="handleChange" type="date" placeholder="选择日期"
                   style="width: 200px"></Date-picker>
    </div>
    <Table border :columns="columns" :data="data"></Table>
  </div>
</template>
<style scoped>
  .date-div1{
    text-align: right;
    margin: 10px;
    margin-bottom: -40px;
  }
  .date-div2{
    text-align:right;
    margin:10px
  }
</style>
<script>
  import AddPlan from '../components/dialog/AddPlan.vue'
  import moment from 'moment'
  export default {
    components: {
      addPlan: AddPlan
    },
    data() {
      return {
        date: new Date(),
        modal: false,
        hasTable: true,
        columns: [{
          title: '日期',
          key: 'create_time',
          render(row){
            return moment(row.create_time).format('YYYY-MM-DD')
          }
        }, {
          title: '学号',
          key: 'stu_id'
        }, {
          title: '姓名',
          key: 'name'
        }, {
          title: '签到状态',
          'key': 'sign',
          render(row){
            return `{{row.sign?'已签到':'未签到'}}`
          }
        }, {
          title: '签到',
          key: 'action',
          width: 150,
          align: 'center',
          render(row, column, index) {
            return `<Checkbox @on-change="handleCheck(${row.sign},${row.id})" v-model="row.sign"></Checkbox>`;
          }
        }],
        data: [],
        form: {}
      }
    },
    created(){
      const _self = this
      const time = moment(this.date).format('YYYY-MM-DD');
      this.$http.get('/api/getsign?time=' + time).then((res) => {
          const data=res.data
        data.forEach(function (element) {
              if(element.sign=="1"){
                  element.sign=true
              }else{
                  element.sign=false
              }
        })
        _self.data = data
        this.hasTable = false
      }, (res) => {
        _self.data = []
        this.hasTable = true
      })
    },
    methods: {
      createTable(){
        const time = moment(this.date).format('YYYY-MM-DD');
        this.$http.post('/api/sign/create?time=' +time).then((res) => {
          this.$Message.success('创建成功!');
          this.handleChange(time)
        }, (res) => {
          this.$Message.success('创建失败!');
        })
      },
      handleChange(date) {
        const _self = this
        this.$http.get('/api/getsign?time=' + date).then((res) => {

          const data=res.data
          data.forEach(function (element) {
            if(element.sign=="1"){
              element.sign=true
            }else{
              element.sign=false
            }
          })
          _self.data = data
          this.hasTable = false
        }, (res) => {
          _self.data = []
          this.hasTable = true
        })
      },
      handleCheck(sign,id){
        this.$http.post('/api/sign/'+id,{sign:!sign}).then((res)=>{
          const _self = this
          const time = moment(this.date).format('YYYY-MM-DD');
          this.$http.get('/api/getsign?time=' + time).then((res) => {
            const data=res.data
            data.forEach(function (element) {
              if(element.sign=="1"){
                element.sign=true
              }else{
                element.sign=false
              }
            })
            _self.data = data
            this.hasTable = false
          }, (res) => {
            _self.data = []
            this.hasTable = true
          })
        })
      }
    }
  }
</script>



