<template>
  <div>
    <div style="text-align:right;margin:10px">
      <Button type="success" icon="plus-round" @click="add">新增学生</Button>
    </div>
    <Table border :columns="columns" :data="data"></Table>
    <Modal :title="title" v-model="modal" class-name="vertical-center-modal" :loading="loading" @on-ok="sbt"
           on-cancel="cancel" :mask-closable="false">
      <add-student :form="form" ref="addStudent"></add-student>
    </Modal>
  </div>
</template>
<script>
  import AddStudent from '../components/dialog/AddStudent.vue'
  export default {
    components: {
      addStudent: AddStudent
    },
    data() {
      return {
        modal: false,
        loading: true,
        title: '新增学生',
        sbt: this.submit,
        selected: '',
        columns: [{
          title: '姓名',
          key: 'name'
        }, {
          title: '学号',
          key: 'id'
        }, {
          title: '性别',
          key: 'sex'
        }, {
          title: '年龄',
          key: 'age'
        }, {
          title: '年级',
          key: 'grade'
        }, {
          title: '操作',
          key: 'action',
          render(row, column, index) {
            return `<i-button type="primary" size="small" @click="show(${row.id},${index})">修改</i-button> <i-button type="error" size="small" @click="remove(${row.id},${index})">删除</i-button>`;
          }
        }],
        data: [],
        form: {
          name: '',
          grade: '',
          age: '',
          sex: '男'
        }
      }
    },
    created(){
      this.getInfo()
    },
    methods: {
      getInfo(){
        const _self = this
        this.$http.get('/api/student/info').then((res) => {
          _self.data = res.data
        })
      },
      submit(){
        this.$nextTick(() => {
          this.loading = true;
        })
        let _form = this.$refs.addStudent.$refs.form
        let data = this.$refs.addStudent.form
        _form.validate((valid) => {
          if (valid) {
            this.$http.post('/api/student/add', data).then((res) => {
              this.$Message.success('创建成功!');
              this.getInfo()
              this.modal = false;
            })
          }
          else {
            this.loading = false
          }
        })
      },
      update(){
        this.$nextTick(() => {
          this.loading = true;
        })
        let _form = this.$refs.addStudent.$refs.form
        let data = this.$refs.addStudent.form
        _form.validate((valid) => {
          if (valid) {
            this.$http.post('/api/student/update/' + this.selected, data).then((res) => {
              this.$Message.success('修改成功!');
//              this.getInfo()
              this.modal = false;
            })
          }
          else {
            this.loading = false
          }
        })
      },
      add(){
        this.title = '新增学生'
        this.modal = true
        this.form = {
          name: '',
          grade: '',
          age: '',
          sex: '男'
        }
        this.sbt = this.submit
      },
      show(id, index) {
        this.selected = id
        this.sbt = this.update
        this.title = '修改信息'
        this.modal = true
        this.form = this.data[index]
      },
      cancel(){

      },
      remove(id, index) {
        this.$http.delete('/api/student/del/' + id).then((res) => {
          this.$Message.success('删除成功!');
          this.data.splice(index, 1);
        })
      }
    }
  }
</script>



