<template>
  <Form ref="form" :model="form1" :rules="ruleValidate" :label-width="80">

    <Form-item label="姓名" prop="id">
      <Input v-model="form1.name" disabled=""></Input>
    </Form-item>
    <Form-item label="学号" prop="id">
      <Input v-model="form1.stu_id" disabled=""></Input>
    </Form-item>
    <Form-item label="应缴金额" prop="should_pay">
      <Input v-model="form1.should_pay" placeholder="请输入金额"></Input>
    </Form-item>
    <Form-item label="实缴金额" prop="pay">
      <Input v-model="form1.pay" placeholder="请输入金额"></Input>
    </Form-item>
    <Form-item label="教师签名" prop="teacher_sign">
      <Input v-model="form1.teacher_sign" placeholder="请签名"></Input>
    </Form-item>
  </Form>
</template>
<script>
  export default {
    data() {
      return {
        ruleValidate: {
          set_id: [{
            required: false,
            message: '姓名不能为空',
            trigger: 'change'
          }]
        }
      }
    },
    props:['form1']
  }
</script>



