<template>
    <Form ref="formValidate" :model="form" :rules="ruleValidate" :label-width="80">
        <Form-item label="姓名" prop="name">
            <Input v-model="form.name" placeholder="请输入姓名" disabled=""></Input>
        </Form-item>
        <Form-item label="考试内容" prop="name">
            <Input v-model="form.exam_name" placeholder="请输入教学计划" disabled=""></Input>
        </Form-item>
           <Form-item label="考试分数" prop="name">
            <Input v-model="form.score" placeholder="请输入考试分数"></Input>
        </Form-item>
        <Form-item label="教师签名" prop="name">
            <Input v-model="form.teacher_sign" placeholder="请签名"></Input>
        </Form-item>
    </Form>
</template>
<script>
    export default {
        data () {
            return {
                ruleValidate: {
                    name: [
                        { required: true, message: '姓名不能为空', trigger: 'blur' }
                    ],
                    gender: [
                        { required: true, message: '请选择性别', trigger: 'change' }
                    ],
                    date: [
                        { required: true, type: 'date', message: '请选择日期', trigger: 'change' }
                    ],
                }
            }
        },
        props:['form'],
        methods: {
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$Message.success('提交成功!');
                    } else {
                        this.$Message.error('表单验证失败!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
            }
        }
    }
</script>



