<template>
  <Form ref="form" :model="form" :rules="ruleValidate" :label-width="80">
    <Form-item label="姓名" prop="name">
      <Input v-model="form.name" placeholder="请输入姓名"></Input>
    </Form-item>
    <Form-item label="年龄" prop="age">
      <Input v-model="form.age" placeholder="请输入年龄"></Input>
    </Form-item>
    <Form-item label="性别" prop="sex">
      <Radio-group v-model="form.sex">
        <Radio label="男">男</Radio>
        <Radio label="女">女</Radio>
      </Radio-group>
    </Form-item>
    <Form-item label="年级" prop="grade">
      <Input v-model="form.grade" placeholder="请输入年级"></Input>
    </Form-item>
  </Form>
</template>
<script>
  export default {
    data() {
      return {
        ruleValidate: {
          name: [{
            required: true,
            message: '姓名不能为空',
            trigger: 'blur'
          }],
          sex: [{
            required: true,
            message: '请选择性别',
            trigger: 'change'
          }],
          age: [{
            required: true,
            message: '年龄不能为空',
            trigger: 'blur'
          }],
          grade: [{
            required: true,
            message: '年级不能为空',
            trigger: 'blur'
          }],
        }
      }
    },
    props: ['form']
  }
</script>



