<template>
  <div>
    <div :class="[hasTable?'date-div1':'date-div2']">
      <Button v-if="hasTable" type="success" icon="plus-round" @click="createTable">创建计划</Button>
    </div>
    <div style="text-align:right;margin:10px">
      <Date-picker v-model="date" @on-change="handleChange" type="date" placeholder="选择日期"
                   style="width: 200px"></Date-picker>
    </div>
    <Table border :columns="columns" :data="data"></Table>
    <Modal title="编辑计划" v-model="modal" class-name="vertical-center-modal" :loading="loading" @on-ok="update"
           on-cancel="cancel" :mask-closable="false">
      <add-plan :form="form" ref="addPlan"></add-plan>
    </Modal>
  </div>
</template>
<style scoped>
  .date-div1 {
    text-align: right;
    margin: 10px;
    margin-bottom: -40px;
  }

  .date-div2 {
    text-align: right;
    margin: 10px
  }
</style>
<script>
  import AddPlan from '../components/dialog/AddPlan.vue'
  import moment from 'moment'
  export default {
    components: {
      addPlan: AddPlan
    },
    data() {
      return {
        date: new Date(),
        modal: false,
        hasTable: false,
        loading:true,
        columns: [{
          title: '日期',
          key: 'create_time',
          render(row){
            return moment(row.create_time).format('YYYY-MM-DD')
          }
        }, {
          title: '学号',
          key: 'stu_id'
        }, {
          title: '姓名',
          key: 'name'
        }, {
          title: ' 教学计划',
          'key': 'content'
        }, {
          title: '完成状态',
          'key': 'status',
          render(row){
              return `{{row.status=="1"?'已完成':'未完成'}}`
          }
        }, {
          title: '教师签名',
          'key': 'teacher_sign'
        }, {
          title: '操作',
          key: 'action',
          width: 150,
          align: 'center',
          render(row, column, index) {
            return `<i-button type="primary" size="small" @click="edit(${index})">编辑</i-button>`;
          }
        }],
        data: [],
        form: {}
      }
    },
    created(){
      const _self = this
      const time = moment(this.date).format('YYYY-MM-DD');
      this.$http.get('/api/plan/info?time=' + time).then((res) => {
        const data = res.data
        data.forEach(function (element) {
          if (element.sign == "1") {
            element.sign = true
          } else {
            element.sign = false
          }
        })
        _self.data = data
        this.hasTable = false
      }, (res) => {
        _self.data = []
        this.hasTable = true
      })
    },
    methods: {
      createTable(){
        const time = moment(this.date).format('YYYY-MM-DD');
        this.$http.post('/api/plan/create?time=' +time).then((res) => {
          this.$Message.success('创建成功!');
          this.handleChange(time)
        }, (res) => {
          this.$Message.success('创建失败!');
        })
      },
      handleChange(date) {
        const _self = this
        this.$http.get('/api/plan/info?time=' + date).then((res) => {

          const data = res.data
          _self.data = data
          this.hasTable = false
        }, (res) => {
          _self.data = []
          this.hasTable = true
        })
      },
      edit(index) {
        this.form = this.data[index]
        this.modal = true

      },
      update(){
        this.$nextTick(() => { this.loading = true; })
        let _form=this.$refs.addPlan.$refs.form
        let data=this.$refs.addPlan.form
        _form.validate((valid) => {
          if (valid) {
            this.$http.post('/api/plan/update/'+this.form.id,data).then((res) => {
              this.$Message.success('修改成功!');
              this.modal=false;
            })
          }
          else{
            this.loading=false
          }
        })
      },
    }
  }
</script>



