<template>
  <div>
    <div style="text-align:right;margin:10px">
      <Button type="success" icon="plus-round" @click="modal=true">创建缴费</Button>
    </div>
    <Table border :columns="columns" :data="data"></Table>
    <Modal title="创建缴费" v-model="modal" class-name="vertical-center-modal" :loading="loading" @on-ok="submit"
           :mask-closable="false">
      <add-pay ref="addPay"></add-pay>
    </Modal>
    <Modal title="编辑缴费" v-model="modal1" class-name="vertical-center-modal" :loading="loading" @on-ok="editFinance"
           :mask-closable="false">
      <edit-pay :form1="form1" ref="editPay"></edit-pay>
    </Modal>
  </div>
</template>
<script>
  import AddPay from '../components/dialog/AddPay.vue'
  import EditPay from '../components/dialog/EditPay.vue'
  import moment from 'moment'
  export default {
    components: {
      addPay: AddPay,
      editPay: EditPay
    },
    data() {
      return {
        date: new Date(),
        modal: false,
        modal1: false,
        loading: true,
        columns: [{
          title: '修改日期',
          key: 'create_time',
          render(row){
            return moment(row.create_time).format('YYYY-MM-DD')
          }
        }, {
          title: '学号',
          key: 'stu_id'
        }, {
          title: '姓名',
          key: 'name',
        }, {
          title: '应缴(元)',
          'key': 'should_pay'
        }, {
          title: '实缴(元)',
          'key': 'pay'
        }, {
          title: '缴费状态',
          'key': 'status',
          render(row){
            return `{{row.status=="1"?'已缴清':'未缴清'}}`
          }
        }, {
          title: '教师签名',
          'key': 'teacher_sign',
        }, {
          title: '操作',
          key: 'action',
          render(row, column, index) {
            return `<i-button type="primary" size="small" @click="edit(${index})">编辑</i-button><i-button type="error" size="small" @click="remove(${row.id},${index})">删除</i-button>`;
          }
        }],
        data: [],
        form: {},
        form1: {}
      }
    },
    created(){
      this.getFinance()
    },
    methods: {
      submit(){
        this.$nextTick(() => {
          this.loading = true;
        })
        let _form = this.$refs.addPay.$refs.form
        let data = this.$refs.addPay.form
        if (parseInt(data.should_pay) > parseInt(data.pay)) {
          data.status = false
        } else {
          data.status = true
        }
        data.create_time = moment(new Date).format('YYYY-MM-DD')
        _form.validate((valid) => {
          if (valid) {
            if (!data.stu_id) {
              this.$Message.success('选择一个学生!')
              this.loading = false
            } else {
              this.$http.post('/api/finance/add', data).then((res) => {
                this.$Message.success('创建成功!');
                this.getFinance()
                this.modal = false;
              })
            }

          }
          else {
            this.loading = false
          }
        })
      },
      getFinance(){
        const _self = this
        this.$http.get('/api/finance/info').then((res) => {
          _self.data = res.data

        })
      },
      handleChange(date) {
        console.log(date)
      },
      edit(index) {
        this.modal1 = true
        this.form1 = this.data[index]
      },
      editFinance(){
        this.$nextTick(() => {
          this.loading = true;
        })
        let _form = this.$refs.editPay.$refs.form
        let data = this.$refs.editPay.form1
        if (parseInt(data.should_pay) > parseInt(data.pay)) {
          data.status = false
        } else {
          data.status = true
        }
        data.create_time = moment(new Date).format('YYYY-MM-DD')
        _form.validate((valid) => {
          if (valid) {
              this.$http.post('/api/finance/update/'+data.id, data).then((res) => {
                this.$Message.success('修改成功!');
                this.getFinance()
                this.modal1 = false;
              })
          }
          else {
            this.loading = false
          }
        })
      },
      remove(id, index) {
        this.$http.delete('/api/finance/del/' + id).then((res) => {
          this.$Message.success('删除成功!');
          this.data.splice(index, 1);
        })
      }
    }
  }
</script>



