<template>
  <div>
    <div :class="[hasTable?'date-div1':'date-div2']">
      <Button v-if="hasTable" type="success" icon="plus-round" @click="modal=true">创建成绩单</Button>
    </div>
    <div style="text-align:right;margin:10px">
      <Date-picker v-model="date" @on-change="handleChange" type="date" placeholder="选择日期"
                   style="width: 200px"></Date-picker>
    </div>
    <Table border :columns="columns" :data="data"></Table>
    <Modal title="新建成绩单" v-model="modal" class-name="vertical-center-modal" :loading="loading" @on-ok="submit"
           on-cancel="cancel" :mask-closable="false">
      <Input v-model="exam_name" placeholder="请输入考试内容"/>
    </Modal>
    <Modal title="录入成绩" v-model="modal1" class-name="vertical-center-modal" :loading="loading" @on-ok="update"
           on-cancel="cancel" :mask-closable="false">
      <add-grade :form="form"></add-grade>
    </Modal>
  </div>
</template>
<style scoped>
  .date-div1 {
    text-align: right;
    margin: 10px;
    margin-bottom: -40px;
  }

  .date-div2 {
    text-align: right;
    margin: 10px
  }
</style>
<script>
  import AddGrade from '../components/dialog/AddGrade.vue'
  import moment from 'moment'
  export default {
    components: {
      addGrade: AddGrade
    },
    data() {
      return {
        date: new Date(),
        modal: false,
        modal1: false,
        hasTable: true,
        loading: true,
        exam_name: "",
        columns: [{
          title: '日期',
          key: 'create_time',
          render(row){
            return moment(row.create_time).format('YYYY-MM-DD')
          }
        }, {
          title: '学号',
          key: 'stu_id'
        }, {
          title: '姓名',
          key: 'name'
        }, {
          title: ' 考试内容',
          'key': 'exam_name'
        }, {
          title: '考试分数',
          'key': 'score'
        }, {
          title: '教师签名',
          'key': 'teacher_sign'
        }, {
          title: '操作',
          key: 'action',
          width: 150,
          align: 'center',
          render(row, column, index) {
            return `<i-button type="primary" size="small" @click="edit(${index})">编辑</i-button>`;
          }
        }],
        data: [],
        form: {}
      }
    },
    created(){
      const _self = this
      const time = moment(this.date).format('YYYY-MM-DD');
      this.$http.get('/api/grade/info?time=' + time).then((res) => {
        const data = res.data
        data.forEach(function (element) {
          if (element.sign == "1") {
            element.sign = true
          } else {
            element.sign = false
          }
        })
        _self.data = data
//        this.hasTable = false
      }, (res) => {
        _self.data = []
//        this.hasTable = true
      })
    },
    methods: {
      createTable(){

      },
      handleChange(date) {
        const _self = this
        this.$http.get('/api/grade/info?time=' + date).then((res) => {

          const data = res.data
          _self.data = data
//          this.hasTable = false
        }, (res) => {
          _self.data = []
//          this.hasTable = true
        })
      },
      edit(index) {
        this.form = this.data[index]
        this.modal1 = true
      },
      update(){
          const id = this.form.id
        this.$nextTick(() => { this.loading = true; })
          if (this.form.score||this.form.teacher_sign) {
            this.$http.post('/api/grade/update/'+id,this.form).then((res) => {
              this.$Message.success('修改成功!');
              this.modal1=false;
            })
          }
          else{
            this.loading=false
            this.modal1=false;
          }
      },
      submit(){
        this.$nextTick(() => {
          this.loading = true;
        })
        if(this.exam_name){
          const data={};
          const time = moment(this.date).format('YYYY-MM-DD');
          data.create_time=time
          data.exam_name=this.exam_name
          this.$http.post('/api/grade/create?time=' + time,data).then((res) => {
            this.$Message.success('创建成功!');
            this.handleChange(time)
            this.modal=false;
          }, (res) => {
            this.$Message.success('创建失败!');
          })
        }
        else{
          this.$Message.success('请填写考试名称!');
          this.loading=false
        }

      }
    }
  }
</script>



