import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import Finance from '@/pages/finance'
import Grade from '@/pages/Grade'
import Plan from '@/pages/Plan'
import Sign from '@/pages/Sign'
import login from '@/pages/login'
import home from '@/pages/home'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'login',
    component: login
  }, {
    path: '/home',
    name: 'home',
    component: home,
    children:[
      {
        path: '/',
        name: 'index',
        component: index
      },
      {
        path: 'finance',
        name: 'Finance',
        component: Finance
      }, {
        path: 'grade',
        name: 'Grade',
        component: Grade
      }, {
        path: 'plan',
        name: 'Plan',
        component: Plan
      }, {
        path: 'sign',
        name: 'Sign',
        component: Sign
      }
    ]
  }]

})



