<template>
  <Form ref="form" :model="form" :rules="ruleValidate" :label-width="80">

    <Form-item label="姓名" prop="id">
      <Select v-model="form.stu_id" style="width:200px">
        <Option v-for="item in users" :value="item.id" :key="item">{{ item.name }}</Option>
      </Select>
    </Form-item>
    <Form-item label="学号" prop="id">
      <Input v-model="form.stu_id" :value="form.id" disabled=""></Input>
    </Form-item>
    <Form-item label="应缴金额" prop="should_pay">
      <Input v-model="form.should_pay" placeholder="请输入金额"></Input>
    </Form-item>
    <Form-item label="实缴金额" prop="pay">
      <Input v-model="form.pay" placeholder="请输入金额"></Input>
    </Form-item>
    <Form-item label="教师签名" prop="teacher_sign">
      <Input v-model="form.teacher_sign" placeholder="请签名"></Input>
    </Form-item>
  </Form>
</template>
<script>
  export default {
    data() {
      return {
        users: [],
        form:{
          stu_id:"",
          should_pay:"",
          pay:"",
          teacher_sign:""
        },
        ruleValidate: {
          set_id: [{
            required: false,
            message: '姓名不能为空',
            trigger: 'change'
          }],
          should_pay: [{
            required: true,
            message: '请输入金额',
            trigger: 'blur'
          }],
          pay: [{
            required: true,
            message: '请输入金额',
            trigger: 'blur'
          }]
        }
      }
    },
    created(){
      this.getInfo()
    },
    methods: {
      getInfo(){
        const _self = this
        this.$http.get('/api/student/info').then((res) => {
          _self.users = res.data

        })
      }
    }
  }
</script>



